class Sponsors extends Component {
  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }
  next() {
    this.slider.slickNext();
  }
  previous() {
    this.slider.slickPrev();
  }
  render() {
    var settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 1,
      swipeToSlide: true,
      arrows: false,
      autoplay: false,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 1,
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    };
    return (
      <div className="sponsors-container">
        <div className="sponsors-content">
          <div className="sponsors-title">
            <h3>Our Sponsors</h3>
          </div>
          <div className="sponsors-lists">
            <div className="sponsors-arrow-prev">
              <img src="assets/img/Sponsors/arrow-left.svg" alt="arrow_left" onClick={this.previous} />
            </div>
            <Slider ref={c => (this.slider = c)} {...settings}>
              <div key={1}>
                <img src="assets/img/Sponsors/google.svg" alt="Google_Sponsors" />
              </div>
              <div key={2}>
                <img src="assets/img/Sponsors/andela.svg" alt="Andela_Sponsors" />

              </div>
              <div key={3}>
                <img src="assets/img/Sponsors/afford.svg" alt="Afford_Sponsors" />

              </div>

              <div key={4}>
                <img src="assets/img/Sponsors/fb.svg" alt="Facebook_Sponsors" />

              </div>

              <div key={5}>
                <img src="assets/img/Sponsors/afford.svg" alt="Afford_Sponsors" />

              </div>

            </Slider>
            <div className="sponsors-arrow-next">
              <img src="assets/img/Sponsors/arrow-right.svg" alt="arrow_right" onClick={this.next} />
            </div>
          </div>
        </div>
      </div>
    )
  }

}


function createMarkup($tags) {
  return {__html: $tags};
}

