import React, { Component } from 'react';
import { Link, NavLink, } from 'react-router-dom';
import _ from 'lodash';

import DataStore from '../../scripts/flux/stores/DataStores.js';

import {
  Collapse,
  Navbar,
  NavbarBrand,
  Nav,
  NavItem,
} from 'reactstrap';


class MenuNavBar extends Component {
  render() {

    return (
      <div>
        <MyMenu />
      </div>

    )
  }
}


const imagesPath = {
  minus: "assets/img/cancel.svg",
  plus: "assets/img/menu-nav.svg"
}



class MyMenu extends Component {

  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });

  }
  getImageName = () => this.state.isOpen ? 'minus' : 'plus'

  render() {
    const imageName = this.getImageName();
    let allPages = DataStore.getAllPages();
    allPages = _.sortBy(allPages, [function (page) { return page.menu_order; }]); //sort the menu by order
    return (
      <header>
        <Navbar className="navbar bg-transparent nav-header" Style="max-width: 1205px;">
          <div className="toggle-menu">
            <Link to="#">
              <img src={imagesPath[imageName]} alt="menu_nav" onClick={this.toggle} />
            </Link>
          </div>
          <div className="">
            <NavbarBrand className="" Style="margin-left: 25%">
              <NavLink to="/" >
                <img src="assets/img/techhut_logo.svg" alt="techhut_logo" />
              </NavLink>
            </NavbarBrand>
          </div>
          <div className="donate-board">
            <Link to="/">Donate</Link>
          </div>
        </Navbar>
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="" navbar>
            <NavItem>
              <NavLink to="/" exact={true} activeClassName="active" onClick={this.toggle} >Home</NavLink>
            </NavItem>
            {allPages.map((page) => {
              if (page.slug !== 'home') {
                return (
                    <NavItem key={page.id}>
                      <NavLink 
                      to={`/${page.slug}`} activeClassName="active" onClick={this.toggle} >{page.title.rendered}</NavLink>
                    </NavItem>
                    
                )
              }
            })
            }

            <NavItem className="social-link-container">
              <p><a href="/contact" Style="margin-right: 43px;">Find out more </a><a href="https://facebook.com/techhut"><img src="assets/img/iconmonstr-facebook-6.svg" alt="" /></a><a href="https://instagram.com/techhut"><img src="assets/img/iconmonstr-instagram-11.svg" alt="" /></a> <a href="https://youtube.com/techhut"><img src="assets/img/iconmonstr-youtube-1.svg" alt="" /></a></p>
            </NavItem>
          </Nav>
        </Collapse>
      </header>
    )
  }

}



export default MenuNavBar;